.qazana-piechart{position:relative;text-align:center}.qazana-piechart canvas{position:absolute;top:0;left:50%;z-index:1;margin-bottom:13px;max-width:100%;transform:rotateY(180deg) translateX(50%)}.qazana-piechart-counterclockwise-yes canvas{transform:rotateY(0) translateX(-50%)}.qazana-piechart-title{color:#666666;text-align:center;font-weight:400;font-size:19px;line-height:2.5}.qazana-piechart-subtitle{color:#666666;text-align:center;font-weight:400;font-size:14px;line-height:1.5}.qazana-piechart-number-progress{display:flex;align-items:center;justify-content:center;letter-spacing:-2px;font-size:35px}.qazana-piechart-number-progress-prefix{flex-grow:1;text-align:right}.qazana-piechart-number-progress-suffix{flex-grow:1;text-align:left;font-size:70%}.qazana-piechart .piechart-number{position:relative;overflow:hidden;margin:0 auto;margin-bottom:22px;width:160px;height:160px;border-radius:50%;line-height:160px}.qazana-piechart .piechart-number:before{position:absolute;top:0;right:0;bottom:0;left:0;border:1px solid grey;border-radius:inherit;content:''}.qazana-piechart .piechart-number>*{line-height:inherit}
